body {
    background-color: green;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}


ul {
    display: inline-block;
    margin: 0;
    padding: 0;
    list-style: none;
}

li {
    margin: 0;
    padding: 0;
    width: 5em;
    height: 7em;
    background: #eee;
    border: 1px solid black;
    border-radius: 5px;
    margin-bottom: -80px;
    clear: both;
}

li:last-child {
    margin-bottom: 0;
}

.hand {
    display: block;
}  

.card {
    width: 100%;
}

.chatbox {
    position: absolute;
    width: 20%;
    height: 50%;
    overflow: scroll;
    right: 1em;
    bottom: 12em;
    background-color: white;
    border-style: solid;
    border-color: black;
    border-radius: 10px;
    padding: .5em;
}

.chatform {
    position: absolute;
    width: 20%;
    height: 25%;
    overflow: scroll;
    right: 1em;
    bottom: 1em;
    background-color: white;
    border-style: solid;
    border-color: black;
    border-radius: 10px;
    padding: .5em;
}

.main {
    min-height: 100vh;
    min-width: 100vh;
    /* display: flex; */
    background-color: green;
    /* position: absolute; */

}

.welcome {
    position: fixed;
    top: 3px;
    left: 3px;
}

.chip {
    height: 50px;
    width: 50px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-flex;
    cursor: pointer;
    align-items: center; 
    justify-content: center;
    margin: 5px;
}

.chips-bet {
    height: 50px;
    width: 50px;
    background-color: white;
    border-radius: 50%;
    display: inline-flex;
    cursor: pointer;
    align-items: center; 
    justify-content: center;
    margin: 5px;
}

.chip-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.button {
    margin: 5px;
}
.player-container {
    min-width: 10em;
    min-height: 5em;
    align-items: center;
    justify-content: center;
}

.username {
    display: block;
}

.bet-area {
    -moz-border-radius: 50px/50px;
    -webkit-border-radius: 50px 50px;
    border-radius: 50px/50px;
    border: solid 2px white;
    width: 100px;
    height: 100px;
    display: inline-flex;
    align-items: center; 
    justify-content: center;
  }

.row-container {
    align-self: center;
    justify-content: center;
    min-width: 100%;
    min-height: 100%;
}

.slides-bg {
    position:relative;
    height: 80vh;
    background-color: white;
    border-style: solid;
    border-color: black;
    padding: 10px;
}
  
.slides-controls {
    position: absolute;
    box-sizing: content-box;
    background-color: #000000;
    margin-top: 1em;
    bottom: -2em;
    left: -2em;
    right: -2em;
    width: 100%;
    padding:1em 2em;
    display: flex;
    justify-content: space-between;
}

.slide-image {
    border-radius: 10px;
    padding: 20px;
    margin: 1em;
    -moz-box-shadow: 0 0 10px #000000;
    -webkit-box-shadow: 0 0 10px #000000;
    box-shadow: 0 0 10px #000000;
}

.slide-inactive {
    display: none;
}
  
.slide-active {
    display: inline-block;
    width:100%;
    height:100%;
    overflow-y: hidden;
}

.circle {
    width: 10px;
    height: 10px;
    border-radius: 5px;
    transform: translate(50%);
}
  
.indicator-inactive {
    background-color:#CCCCCC;
}
  
.indicator-active {
    background-color: #8cd0f0;
}

@media only screen and (max-width: 768px) {
    .slides-controls {
        position: fixed;
        bottom: 0;
        left:0;
        right:0;
        box-sizing: border-box;
    }
}